import React from 'react';
import { FaPlus, FaMinus} from 'react-icons/fa'
import { Stack, IconButton, Text } from '@chakra-ui/core'

interface SpeedDialItemProps {
  icon: any, //Must pass an iconType object
  label: string
  clickHandler: () => void,
  yPos?: string
}

export const SpeedDialItem: React.FC<SpeedDialItemProps> = ({ icon, label, clickHandler, yPos }: SpeedDialItemProps) => {
  return (
    <Stack isInline
      position="fixed"
      bottom={yPos}
      right="20px"
      align="center">
      <Text fontSize={11}>{label}</Text>
      <IconButton aria-label="wallet" icon={icon} isRound onClick={async () => clickHandler()} />
    </Stack>
  )
}

export const SpeedDial: React.FC = ({ children }) => {
  const [isOpen, setOpen] = React.useState(false)
  const wrapperRef = React.useRef(null);
  //@ts-ignore
  const items = children && children.filter((child: React.ReactNode) => React.isValidElement(child))
  var yPos = 100
  const useOutsideAlerter = (ref: any) => {
    React.useEffect(() => {
      function handleClickOutside(event: MouseEvent) {
        if (ref.current && !ref.current.contains(event.target)) {
          setOpen(false)
        }
      }
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, [ref]);
  }
  useOutsideAlerter(wrapperRef);

  return (<div ref={wrapperRef} style={{ position: "fixed", bottom: "50px", right: "20px" }}>
    <IconButton aria-label="open" isRound icon={isOpen ? <FaMinus /> : <FaPlus />} onClick={() =>setOpen(!isOpen) } style={{ zIndex: 9999}}/>

    {isOpen &&items!.map((child : React.ReactNode, index: number) => {
      if (React.isValidElement(child)) {
        return React.cloneElement(child, {...child.props,yPos: (yPos + 50*(index)).toString()+'px'})
      }} )}
  </div>)
}
